.card {
    border-radius: 8px;
    padding: 16px;
    width: 300px;
    transition: background-color 0.3s ease, color 0.3s ease;
  }
  
  .light {
    background-color: #ffffff;
    color: #333333;
  }
  
  .dark {
    background-color: #333333;
    color: #ffffff;
  }
  
  .card-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .icon {
    font-size: 24px;
    margin-right: 8px;
  }
  
  .toggle-button {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 20px;
  }
  
  .submit-button {
    background-color: #cccccc;
    border: none;
    border-radius: 4px;
    padding: 8px 16px;
    cursor: pointer;
  }
  